<template>
  <div class="page-layout videos" id="vApp">
    <b-alert :show="alertCountdown" dismissible fade :variant="alertType" @dismiss-count-down="countDownChanged" >
      {{alertText}}
    </b-alert>
    <div class="page-header">
    </div>
    <div class="row">
      <div class="col-md-12">
        <div id="" v-if="mobileView">
          <div class="text-center text-danger my-2" v-show="isLoading">
            <b-spinner class="align-middle"></b-spinner>
          </div>
          <div class="holder" v-show="!isLoading">
            <div v-if="videoIsSuccess">
              <div :class="{'bar1':true}"></div>
              <div :class="{'bar':!mobileView}"></div>
              <div :class="{'bar-t':mobileView}"></div>
              <div :class="{'bar-b':mobileView}"></div>
              <div :class="{'bar-l':mobileView}"></div>
              <div :class="{'bar-r':mobileView}"></div>
              <div :class="{'bar2':!mobileView}"></div>
              <iframe class="frame youtube-video" width="100%" height="500" :src="'https://www.youtube.com/embed/' + lessonPath + '?&autoplay=1&modestbranding=1'" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
            </div>
            <div v-else>
              <h3>طلبك قيد اﻻنتظار, من فضلك انتظر موافقة الادارة</h3>
              <br>
            </div>
          </div>
        </div>
        <div v-else>
          <h3>مشاهدة الدرس تكون على جهازك المحمول فقط</h3>
          <br>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import { sendRequest }  from "../utils/RequestHelper.js"
  import $ from "jquery";

  export default {
    name: 'Home',
    data() {
      return {
        videos:[],
        isLoading: false,
        alertType: '',
        alertText: '',
        alertCountdown: 0,
        currentVideo: {},
        lessonPath: null,
        mobileView: true,
        isPaused: false,
        videoIsSuccess: false
      }
    },
    methods: {
      loadData() {
        this.isLoading = true;
          var successCallback = (response) => {            
            if(response.data.path.includes("https://www.youtube.com/")) {
              var url = new URL(response.data.path);
              this.lessonPath = url.searchParams.get("v");
              this.isLoading = false;
              this.videoIsSuccess = true;
              window.onresize = function() {
                if ((window.outerHeight - window.innerHeight) > 100) {
                   var currentInnerHtml;
                  var element = new Image();
                  var elementWithHiddenContent = document.querySelector("#app");
                  var innerHtml = elementWithHiddenContent.innerHTML;
                  element.__defineGetter__("id", function() {
                      currentInnerHtml = ""
                  });
                  setInterval(function() {
                    if(window.location.href.includes("lesson")){
                      currentInnerHtml = innerHtml;
                      console.clear();
                      window.location.replace("/");
                      elementWithHiddenContent.innerHTML = currentInnerHtml;
                    }
                  }, 2000);
                }
              }
            }
            else{
              this.isLoading = false;
              this.alertType = "danger"
              this.alertText = "خطأ - قم بأبلاغ الادارة"
              this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
            }
          };
          var errorCallback = (error) => {
            var errorMessage = "حدث خطأ ما ، حاول قريبًا"
            if(error.response.status == 400)
              errorMessage = error.response.data.message;
            this.isLoading = false;
            this.alertType = "danger"
            this.alertText = errorMessage
            this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
          };
          sendRequest('post', '/students/request/media/confirm', true, { media: this.$route.params.lessonID}, successCallback, errorCallback);
      },
      countDownChanged(alertCountdown) {
        this.alertCountdown = alertCountdown
      }
    },
    created() {
      var userAgent = window.navigator.userAgent,
          platform = window.navigator.platform,
          macosPlatforms = ['Macintosh', 'MacIntel', 'MacPPC', 'Mac68K'],
          windowsPlatforms = ['Win32', 'Win64', 'Windows', 'WinCE'],
          iosPlatforms = ['iPhone', 'iPad', 'iPod'],
          os = null;
      if (macosPlatforms.indexOf(platform) !== -1) {
        os = 'Mac OS';
        this.mobileView = false;
      } else if (iosPlatforms.indexOf(platform) !== -1) {
        os = 'iOS';
        this.mobileView = true;
      } else if (windowsPlatforms.indexOf(platform) !== -1) {
        os = 'Windows';
        this.mobileView = false;
      } else if (/Android/.test(userAgent)) {
        os = 'Android';
        this.mobileView = true;
      } else if (!os && /Linux/.test(platform)) {
        os = 'Linux';
        this.mobileView = false;
      }
      this.isLoading = false;
      // eval(function(p,a,c,k,e,d){e=function(c){return c.toString(36)};if(!''.replace(/^/,String)){while(c--){d[c.toString(a)]=k[c]||c.toString(a)}k=[function(e){return d[e]}];e=function(){return'\\w+'};c=1};while(c--){if(k[c]){p=p.replace(new RegExp('\\b'+e(c)+'\\b','g'),k[c])}}return p}('(3(){(3 a(){8{(3 b(2){7((\'\'+(2/2)).6!==1||2%5===0){(3(){}).9(\'4\')()}c{4}b(++2)})(0)}d(e){g(a,f)}})()})();',17,17,'||i|function|debugger|20|length|if|try|constructor|||else|catch||5000|setTimeout'.split('|'),0,{}))

      $(document).on('keydown', function(event) {
        if (event.keyCode == 9) {   //tab pressed
        console.log("stop");
          event.preventDefault(); // stops its action
        }
      });
      if (this.mobileView) {
        this.loadData();
      }
    }
  }
</script>
